footer{
    margin-top:10vmax;
    padding: 2vmax;
    background-color: rgb(34,35,33);
    color: white;
    display: flex;
    align-items: center;
}

.LeftFooter {
    width: 20%;
    display: flex;

    flex-direction: column;
    align-items: center;
}
.LeftFooter > h4 {
    font-family: "Roboto";
    font-size: 1vmax;
}
.LeftFooter > p {
    text-align: center;
    font-size: 1.2vmax;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande',
     'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.LeftFooter > img {
    width: 8vmax;
    margin: 1vmax;
    cursor: pointer;
   }

.midFooter {
    width: 60%;
    text-align: center;
}
.midFooter > h1 {
    font-size: 4vmax;
    font-family: "Roboto";
    color: #eb4034;
}
.midFooter > p {
   max-width: 50%;
   font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
   margin: 1vmax auto;
}
 .rightFooter {
    width: 20%;

    display: flex;
    flex-direction: column;
    align-items: center;
 }
 .rightFooter > h4{
    font-family: "Roboto";
    font-size: 1.4vmax;
    text-decoration: underline;
 }
 .rightFooter > a{
    text-decoration: none;
    font-size: 1.3vmax;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: azure;
    transition: all 0.5s ;
    margin:0.5vmax;
 }

.rightFooter > a:hover {
    color:#eb4034
}